import { supabase } from '@/lib/supabase';
import { useEffect, useState } from 'react';

interface DashboardStats {
  totalIngredients: number;
  totalRecipes: number;
  totalMenuDishes: number;
  averageDishPrice: number;
}

export default function WebAppDashboard() {
  const [stats, setStats] = useState<DashboardStats>({
    totalIngredients: 0,
    totalRecipes: 0,
    totalMenuDishes: 0,
    averageDishPrice: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        // Fetch ingredients count
        const { count: ingredientsCount } = await supabase
          .from('ingredients')
          .select('*', { count: 'exact', head: true });

        // Fetch recipes count
        const { count: recipesCount } = await supabase
          .from('recipes')
          .select('*', { count: 'exact', head: true });

        // Fetch menu dishes count and average price
        const { data: menuDishes } = await supabase
          .from('menu_dishes')
          .select('selling_price');

        const totalDishes = menuDishes?.length || 0;
        const averagePrice = totalDishes > 0 
          ? menuDishes.reduce((sum, dish) => sum + (dish.selling_price || 0), 0) / totalDishes
          : 0;

        setStats({
          totalIngredients: ingredientsCount || 0,
          totalRecipes: recipesCount || 0,
          totalMenuDishes: totalDishes,
          averageDishPrice: averagePrice,
        });
      } catch (error) {
        console.error('Error fetching stats:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
  }, []);

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-6"></div>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            {[...Array(4)].map((_, i) => (
              <div key={i} className="bg-white p-6 rounded-lg shadow">
                <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                <div className="h-8 bg-gray-200 rounded w-1/2"></div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold text-gray-900 mb-8">
        Kitchen Management Dashboard
      </h1>
      
      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <div className="bg-white p-6 rounded-lg shadow">
          <h3 className="text-sm font-medium text-gray-500">Total Ingredients</h3>
          <p className="text-3xl font-bold text-blue-600">{stats.totalIngredients}</p>
        </div>
        <div className="bg-white p-6 rounded-lg shadow">
          <h3 className="text-sm font-medium text-gray-500">Total Recipes</h3>
          <p className="text-3xl font-bold text-green-600">{stats.totalRecipes}</p>
        </div>
        <div className="bg-white p-6 rounded-lg shadow">
          <h3 className="text-sm font-medium text-gray-500">Menu Dishes</h3>
          <p className="text-3xl font-bold text-purple-600">{stats.totalMenuDishes}</p>
        </div>
        <div className="bg-white p-6 rounded-lg shadow">
          <h3 className="text-sm font-medium text-gray-500">Avg Dish Price</h3>
          <p className="text-3xl font-bold text-orange-600">
            ${stats.averageDishPrice.toFixed(2)}
          </p>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-white p-6 rounded-lg shadow">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Quick Actions</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <a
            href="/webapp/ingredients"
            className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <h3 className="font-medium text-gray-900">Manage Ingredients</h3>
            <p className="text-sm text-gray-500">Add, edit, and organize your ingredients</p>
          </a>
          <a
            href="/webapp/recipes"
            className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <h3 className="font-medium text-gray-900">Create Recipes</h3>
            <p className="text-sm text-gray-500">Build recipes with ingredient lists</p>
          </a>
          <a
            href="/webapp/cogs"
            className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <h3 className="font-medium text-gray-900">Calculate COGS</h3>
            <p className="text-sm text-gray-500">Analyze costs and profit margins</p>
          </a>
        </div>
      </div>

      {/* Getting Started */}
      <div className="mt-8 bg-blue-50 p-6 rounded-lg">
        <h2 className="text-lg font-semibold text-blue-900 mb-2">Getting Started</h2>
        <p className="text-blue-700">
          Welcome to PrepFlow! Start by adding your ingredients, then create recipes to calculate your Cost of Goods Sold (COGS).
        </p>
      </div>
    </div>
  );
}
